import _ from 'lodash';

const randomCharFromString = (strings: Array<string>) => {
  return (): string => {
    return strings[_.random(strings.length - 1)];
  };
};

export const randomHappyFace: () => string = randomCharFromString(
  ['😁','😃','😄','😆','😉','😊','😋','😌','😍','😇','😎', '🥰',
   '🤗', '🙃', '😻']);

export const randomSadFace: () => string = randomCharFromString(
  ['🤔' , /*'😤','😐','😫','😠','😞','😖','😒'*/]);
